.App {
  text-align: center;
}

.App-logo {
  height: 60vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Big-Lukas {
  position: absolute;

  font-size: 30vh;
  height: 30vh;
  width: 100vw;
  top: -20vh;
  left: -2vw;
}

.Big-Viskanta {
  position: absolute;
  font-size: 30vh;
  height: 30vh;
  width: 100vw;
  top: 40vh;
  left: 40vw;
}

.Page {
  background-color: purple;
  height: 100vh;
}

.Satan {
  position: relative;
  height: 50px;
  width: 50px;
  display: inline-block;
  left: 10px;
}

.Nav-Text {
  display: inline-block;
  position: relative;
  bottom: 10px;
  left: 20px;
  color: white;
}

.NavBar {
  position: fixed;
  background-color: blue;
  width: 100%;
  height: 7%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
